import React, { Component } from 'react';
import * as firebase from 'firebase';
import './App.css';

class App extends Component {
  constructor(){
    super()

    this.state = {
      nick: "noName",
      wiadomosci: [],
    }
  }
  zaloguj(){
    let nickName = document.querySelector(".textBox");
    if(nickName.value!==""){
    this.setState({nick: nickName.value})
    nickName.value = "";
    }
  }
  componentWillMount(){
    let wiadomosciRef = firebase.database().ref('wiadomosci').orderByKey().limitToLast(100);
    wiadomosciRef.on('child_added', snapshot =>{
      let wiadomosc = { text: snapshot.val(), id: snapshot.key};
      this.setState({ 
        wiadomosci: [wiadomosc].concat(this.state.wiadomosci)
      })
    })
  }
  Czatuj(){
    let tekst = document.querySelector(".textBox");
    if(tekst.value!==""){
      firebase.database().ref('wiadomosci').push(this.state.nick+" > "+tekst.value);
      tekst.value = '';
    }
  }
  render() {
    if(this.state.nick==="noName"){
      return(
        <div className="App">
          <div className="chatBox">
            <ul>
              { 
                this.state.wiadomosci.map( wiadomosc => <li key={wiadomosc.id}>{wiadomosc.text}</li> )
              }
            </ul>
          </div>
          <input type="text" className="textBox" placeholder="Wpisz swój nick"></input>
          <input type="submit" className="send" value="zaloguj" onClick={this.zaloguj.bind(this)}></input>
        </div>
      )
    }else
    return (
      <div className="App">
        <div className="chatBox">
          <ul>
            { 
              this.state.wiadomosci.map( wiadomosc => <li key={wiadomosc.id}>{wiadomosc.text}</li> )
            }
          </ul>
        </div>
        <input type="text" className="textBox" placeholder="Wpisz wiadomość"></input>
        <input type="submit" className="send" value="wyślij" onClick={this.Czatuj.bind(this)}></input>
      </div>
    );
  }
}

export default App;



